<template>
  <div class="home-container">
    <!-- LEFT—PART -->
    <div class="left-container">
      <div style="width:100%;">
        <div style=" float:left;width:24%; margin-top:0%">
          <h1 style="color:white;font-size:40px;">工</h1>
          <h1 style="color:white;font-size:40px;">况</h1>
          <h1 style="color:white;font-size:40px;">视</h1>
          <h1 style="color:white;font-size:40px;">图</h1>
        </div>
        <!-- 概率分布直方图 -->
        <div style=" float:right;width:74%">
          <distribution :graphName="'工况参数概率直方图'" />
        </div>
        <!-- 概率分布直方图+工况数量 -->
        <div style=" float:right;width:74%">
          <distribution-all :graphName="'工况参数概率直方图 + 工况数量'" />
        </div>
      </div>
      <!-- 平行坐标图+工况数量折线图 -->
      <div style="width:100%;display:flex;">
        <div class="probability_line">
          <line-chart />
        </div>
        <div class="main_para_and_control">
          <para-main @datachange="datachange" />
        </div>
      </div>
      <scatter-matrix :number="number" :name="name" />
    </div>
    <!-- CENTER—PART -->
    <div class="center-container">
      <div style="height:20vh;line-height:800%;">
        <font style="color:white;font-size:30px;margin-top:0%;font-weight:bold;">性能视图（非异常检测）</font>
        <div>
          <input type="text" style="float:right;width:8%;margin-top:-6%;margin-right:15%;"
            onkeyup="value=value.replace(/^\D*(\d*(?:\.\d{0,3})?).*$/g, '$1')" ref="workId">
          <input type="button" style="float:right;width:8%;margin-top:-6%;margin-right:5%" value="确定"
            @click="this.confirmSelectIndex">
        </div>
      </div>

      <select id="sel" style="float:left; height:18%; width:10%;margin-top:-8%;margin-left:3%;"
        @change="this.selectIndexChange">
      </select>

      <line-chart-list :workId="this.selectIndex" :fileName="'performance_data.json'" :title="'性能参数概率折线图'" @datachange="datachange" />
     <label style="color: white">
        <input id="check" type="checkbox" name="interest" value="select" @click="this.check">取消全选
      </label>
      <stacked-line :workId="this.selectWorkId" :fileName="'performance_data.json'" :title="'性能参数视图'" :paramsLength=2  :number="number" :name="name" :checkflag="this.checkflag"/>

    </div>
    <!-- RIGHT—PART -->
    <div class="right-container">
      <div style="height:20vh;line-height:800%;">
        <font style="color:white;font-size:30px;margin-top:0%;font-weight:bold;">操作视图（非异常检测）</font>
      </div>
      <line-chart-list :workId="this.selectIndex" :fileName="'operate_data.json'" :title="'操作参数概率折线图'" @datachange="datachange" />
      <stacked-line :workId="this.selectWorkId" :fileName="'operate_data.json'" :title="'操作参数视图'" :paramsLength=5 :number="number" :name="name" :checkflag="this.checkflag"/>
    </div>
  </div>
</template>

<script>
import Distribution from '../components/distribution/distribution.vue'
import paraMain from '../components/paraMain/paraMain.vue'
import paraSub from '../components/paraSub/paraSub.vue'
import ScatterMatrix from '../components/scatterMatrix/scatterMatrix.vue'
import lineChartList from '../components/lineChartList/lineChartList.vue'
import stackedLine from '../components/stackedLine/stackedLine.vue'
import lineChart from '../components/lineChart/lineChart.vue'
import distributionAll from '../components/distributionAll/distributionAll'
// import tbutton from '../components/test/tbutton.vue'

export default {
  name: 'Home',
  components: { paraMain, paraSub, ScatterMatrix, Distribution, lineChartList, stackedLine, lineChart, distributionAll },
  data () {
    return {
      selectIndex: 7,
      selectWorkId: [7],
      number: null,
      name: '',
      checkflag: null,
      maxSelectLength: 100
    }
  },
  computed: {

  },
  created () {
  },
  mounted () {
    this.$on('showIndex', function (data) {
      // 判断选择的工况数量
      if (data.length > this.maxSelectLength) {
        this.$confirm('筛选的工况数量已大于100，绘制量过大，是否取消？', '提示', {
          confirmButtonText: '取消执行',
          cancelButtonText: '继续执行',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'info',
            message: '已取消绘制任务'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '加载中，请稍等...'
          })
          console.log('继续执行')
          this.selectWorkId = data
          let selectDom = document.getElementById('sel')
          if (!selectDom) return
          // 更新option 动态删除所有的option框
          selectDom.options.length = 0
          for (let i = 0; i < this.selectWorkId.length; i++) {
            selectDom.options.add(new Option(this.selectWorkId[i], this.selectWorkId[i]))
          }
          this.selectIndex = this.selectWorkId[0]
        })
      } else {
        this.selectWorkId = data
        let selectDom = document.getElementById('sel')
        if (!selectDom) return
        // 更新option 动态删除所有的option框
        selectDom.options.length = 0
        for (let i = 0; i < this.selectWorkId.length; i++) {
          selectDom.options.add(new Option(this.selectWorkId[i], this.selectWorkId[i]))
        }
        this.selectIndex = this.selectWorkId[0]
      }
    })
  },
  methods: {
    datachange (data1, data2) {
      this.number = data1
      this.name = data2
    },
    confirmSelectIndex () {
      if (this.$refs.workId.value !== '') { this.selectIndex = this.$refs.workId.value }
    },
    selectIndexChange () {
      let selectDom = document.getElementById('sel')
      this.selectIndex = parseInt(selectDom.options[selectDom.selectedIndex].value)
    },
    check () {
      let checkDom = document.getElementById('check')
      // console.log('ccc', checkDom)
      this.checkflag = checkDom.checked
      // console.log('ccc2', this.checkflag)
    }
  }
}
</script>

<style>
.home-container {
  position: absolute;
  margin: 0;
  width: 99%;
  box-sizing: border-box;
  /* border: 1px solid #000; */
  /* height: 156vh; */
  /* background: url(../assets/images/bg_body.jpg) no-repeat; */
  /* background-size: 200% 200%; */
}
.left-container {
  position: absolute;
  margin-left: 0%;
  width: 50%;
  /* border: 1px solid #000; */
}
.center-container {
  position: absolute;
  margin-left: 51%;
  width: 50%;
  /* border: 1px solid #000; */
}
.right-container {
  position: absolute;
  margin-left: 102%;
  width: 50%;
  /* border: 1px solid #000; */
}
.main_para_and_control {
  /* border: 1px solid #000; */
  width: 80%;
  background: url(../assets/images/t_border.png) no-repeat;
  background-size: 100% 100%;
  margin-top: 1%;
}
.probability_line {
  /* border: 1px solid #000; */
  width: 20%;
  background: url(../assets/images/t_border.png) no-repeat;
  background-size: 100% 100%;
  margin-top: 1%;
}
/* body {
  background: #42413C;
  margin: 0;
  padding: 0;
  text-align: center;
  overflow: hidden;
} */
</style>



