<template>
  <div class="sub_para_container" ref="scatter_matrix">
  </div>
</template>

<script>
export default {
  name: 'scatterMatrix',
  props: {
    number: Number,
    name: String
  },
  data () {
    return {
      filePath1: './static/data/working_condition_data.csv',
      filePath2: './static/data/condition_num.csv',
      allData: [], // 原始数据
      scatterData: [], // 原始数据+筛选
      quantityData: [], // 原始工况数量数据
      selectQData: [], // 原始工况数量数据+筛选
      chart: null,
      dataLength: 1000, // 显示的数据量长度，
      paramsName: []
    }
  },
  watch: {
    data: function (newData, oldData) {

    },
    name (newval) {
      this.chart.dispose()
      this.drawScatterGraph()
    }
  },
  methods: {
    async readFileData () {
      // 读取工况数量数据
      await this.$axios.get(this.filePath2).then(res => {
        let ret = this.$d3.csvParse(res.data)
        let data = []
        let len = this.dataLength > ret.length ? ret.length : this.dataLength
        for (let i = 0; i < len; i++) {
          let temp = Object.keys(ret[i]).map(item => {
            return parseFloat(ret[i][item])
          })
          // 删除数组第一项元素 即索引
          data.push(temp[1])
        }
        this.quantityData = data
        this.selectQData = this.quantityData
      })
      this.$axios.get(this.filePath1).then(res => {
        let ret = this.$d3.csvParse(res.data)
        let data = []
        // 读取临时工况数量数据（自制）
        let len = this.dataLength > ret.length ? ret.length : this.dataLength
        for (let i = 0; i < len; i++) {
          let temp = Object.keys(ret[i]).map(item => {
            return parseFloat(ret[i][item])
          })
          data.push(temp)
        }
        this.allData = data
        this.scatterData = data
        // console.log(this.scatterData)
        this.drawScatterGraph()
      })
    },
    retrieveScatterData (data, dimX, dimY) {
      let result = []
      for (let i = 0; i < data.length; i++) {
        let item = [data[i][dimX], data[i][dimY], this.selectQData[i]]
        result.push(item)
      }
      return result
    },
    isInArray (arr, value) {
      for (var i = 0; i < arr.length; i++) {
        if (value === arr[i]) {
          return true
        }
      }
      return false
    },
    generateGrids (CATEGORY_DIM_COUNT) {
      const GAP = 2
      const BASE_LEFT = 8
      const BASE_TOP = -7
      const GRID_WIDTH = 13
      const GRID_HEIGHT = 12
      const SYMBOL_SIZE = 3
      let index = 0
      const grid = []
      const xAxis = []
      const yAxis = []
      const series = []
      this.paramsName = [
        localStorage.getItem('name1'),
        localStorage.getItem('name2'),
        localStorage.getItem('name3'),
        localStorage.getItem('name4'),
        localStorage.getItem('name5'),
        localStorage.getItem('name6'),
        localStorage.getItem('name7')
      ]
      for (let i = 0; i < CATEGORY_DIM_COUNT; i++) {
        for (let j = 0; j < CATEGORY_DIM_COUNT; j++) {
          if (j <= i) {
            continue
          }
          grid.push({
            left: BASE_LEFT + i * (GRID_WIDTH + GAP) + '%',
            top: BASE_TOP + j * (GRID_HEIGHT + GAP) + '%',
            width: GRID_WIDTH + '%',
            height: GRID_HEIGHT + '%'
          })
          xAxis.push({
            name: j === (CATEGORY_DIM_COUNT - 1) ? this.paramsName[i] : '',
            nameLocation: 'center',
            nameTextStyle: {
              padding: [5, 0, 0, 0],
              fontSize: 10
            },
            splitNumber: 3,
            position: 'bottom',
            axisLine: {
              show: j === CATEGORY_DIM_COUNT - 1,
              onZero: false,
              lineStyle: {
                color: 'white'
              }
            },
            axisTick: {
              show: j === CATEGORY_DIM_COUNT - 1,
              inside: true
            },
            axisLabel: {
              show: j === CATEGORY_DIM_COUNT - 1,
              fontSize: 10
            },
            type: 'value',
            gridIndex: index,
            scale: true
          })
          yAxis.push({
            name: i === 0 ? this.paramsName[j] : '',
            nameLocation: 'center',
            nameTextStyle: {
              padding: [0, 0, 14, 0],
              fontSize: 10
            },
            splitNumber: 3,
            position: 'left',
            axisLine: {
              show: i === 0,
              onZero: false,
              lineStyle: {
                color: 'white'
              }
            },
            axisTick: {
              show: i === 0,
              inside: true
            },
            axisLabel: {
              show: i === 0,
              fontSize: 10
            },
            type: 'value',
            gridIndex: index,
            scale: true
          })
          series.push({
            type: 'scatter',
            symbolSize: SYMBOL_SIZE,
            xAxisIndex: index,
            yAxisIndex: index,
            data: this.retrieveScatterData(this.scatterData, i, j)
          })
          index++
        }
      }
      return {
        grid,
        xAxis,
        yAxis,
        series
      }
    },
    drawScatterGraph () {
      const CATEGORY_DIM_COUNT = 7
      const gridOption = this.generateGrids(CATEGORY_DIM_COUNT)
      // console.log('gridOption: ', gridOption)
      this.chart = this.$echarts.init(this.$refs.scatter_matrix)
      let option = {
        animation: false,
        title: {
          left: 'center',
          text: '工况参数矩阵散点图',
          padding: [20, 0],
          textStyle: {
            color: 'white'
          }
        },
        brush: {
          brushLink: 'all',
          xAxisIndex: gridOption.xAxis.map(function (_, idx) {
            return idx
          }),
          yAxisIndex: gridOption.yAxis.map(function (_, idx) {
            return idx
          }),
          inBrush: {
            opacity: 1
          }
        },
        tooltip: {
          trigger: 'item'
        },
        toolbox: {
          show: false
        },
        visualMap: {
          min: Math.min(...this.quantityData),
          max: 50, // 由于部分工况数量过多，为方便进行对比，max需视情况进行调整
          dimension: 2,
          orient: 'vertical',
          right: 10,
          top: 'center',
          text: ['HIGH', 'LOW'],
          calculable: true,
          inRange: {
            color: ['green', 'yellow', 'red']
          }
        },
        xAxis: gridOption.xAxis,
        yAxis: gridOption.yAxis,
        grid: gridOption.grid,
        series: [
          ...gridOption.series
        ]
      }

      const gloabl = this
      this.$parent.$on('repaint', function (data) {
        gloabl.scatterData = gloabl.allData.filter(function (elem, index) {
          return gloabl.isInArray(data, index)
        })
        gloabl.selectQData = gloabl.quantityData.filter(function (elem, index) {
          return gloabl.isInArray(data, index)
        })
        // console.log(data)
        // console.log(gloabl.scatterData)
        const gridOption = gloabl.generateGrids(CATEGORY_DIM_COUNT)
        option = {
          series: [
            ...gridOption.series
          ]
        }
        gloabl.chart.setOption(option)
      })
      option && this.chart.setOption(option)
      // window.onresize = this.chart.resize
    }
  },
  created () {
  },
  mounted () {
    this.readFileData()
    // this.drawScatterGraph()
  }
}
</script>
<style scoped>
.sub_para_container {
  position: relative;
  /* border: 1px solid rgb(8, 6, 102); */
  background: url(../../assets/images/t_border.png) no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: 60vh;
  margin-left: 0%;
  margin-top: 1%;
}
</style>



