<template>
  <div class="aaa" ref="bbb"></div>
</template>

<script>
import ecStat from 'echarts-stat'

export default {
  name: 'distributionAll',
  data () {
    return {
      filePath: './static/data/working_condition_data.csv',
      filePath1: './static/data/condition_num.csv',
      distributionData: [],
      datamwnum: [],
      dataSet1: [],
      dataLength: 1000 // 显示的数据量长度
    }
  },
  props: ['graphName'],
  watch: {
    data: function (newData, oldData) {
    }
  },
  methods: {
    async readFileData () {
      await this.$axios.get(this.filePath1).then(res => {
        let ret = this.$d3.csvParse(res.data)
        let data = []

        for (let i = 0; i < ret.length; i++) {
          let temp = Object.keys(ret[i]).map(item => {
            return parseFloat(ret[i][item])
          })
          // 删除数组第一项元素 即索引
          temp.shift()
          data.push(temp)
        }
        // dataset表示的是工况id出现的次数
        this.dataSet1 = []
        for (let i = 0; i < data.length; i++) {
          this.dataSet1.push(data[i][0])
        }
      })

      this.$axios.get(this.filePath).then(res => {
        let ret = this.$d3.csvParse(res.data)
        let data = []
        let len = this.dataLength > ret.length ? ret.length : this.dataLength
        for (let i = 0; i < len; i++) {
          let temp = Object.keys(ret[i]).map(item => {
            return parseFloat(ret[i][item])
          })
          // 添加功率id到列表中
          this.datamwnum.push(temp[0])
          temp.shift()
          data.push(temp)
        }
        this.distributionData = data
        this.drawDistributionGraph()
      })
    },
    generateGrids (CATEGORY_DIM_COUNT) {
      const GAP = 4
      const BASE_TOP = 30
      const BASE_LEFT = 6
      const GRID_WIDTH = 9.4
      const GRID_HEIGHT = 58
      const dataset = []
      const grid = []
      const xAxis = []
      const yAxis = []
      const series = []
      const paramsName = ['参数1', '参数2', '参数3', '参数4', '参数5', '参数6', '参数7']
      for (let i = 0; i < CATEGORY_DIM_COUNT; i++) {
        dataset.push({
          transform: {
            type: 'ecStat:histogram',
            config: {
              dimensions: [i],
              method: 'squareRoot'
            }
          }
        })
        grid.push({
          top: BASE_TOP + '%',
          left: BASE_LEFT + i * (GRID_WIDTH + GAP) + '%',
          width: GRID_WIDTH + '%',
          height: GRID_HEIGHT + '%'
        })
        xAxis.push({
          scale: false,
          gridIndex: i,
          splitNumber: 2,
          axisLabel: {
            fontSize: 8,
            color: 'white'
          }
        })
        yAxis.push({
          show: false,
          gridIndex: i,
          splitNumber: 3,
          axisLabel: {
            fontSize: 10
          }
        })
        series.push({
          name: paramsName[i],
          type: 'bar',
          xAxisIndex: i,
          yAxisIndex: i,
          barWidth: '200%',
          label: {
            show: false,
            position: 'top'
          },
          datasetIndex: (i + 1)
        })
      }
      return {
        dataset,
        grid,
        xAxis,
        yAxis,
        series
      }
    },
    drawDistributionGraph () {
      const CATEGORY_DIM_COUNT = 7
      this.$echarts.registerTransform(ecStat.transform.histogram)
      var myChart = this.$echarts.init(this.$refs.bbb)
      const gridOption = this.generateGrids(CATEGORY_DIM_COUNT)

      var option = {
        dataset: [{
          source: this.distributionData
        },
        ...gridOption.dataset
        ],
        tooltip: {},
        grid: gridOption.grid,
        xAxis: gridOption.xAxis,
        yAxis: gridOption.yAxis,
        series: gridOption.series,
        title: {
          left: 'center',
          text: this.graphName,
          padding: [20, 0],
          textStyle: {
            fontSize: 15,
            color: 'white'
          }
        }
      }
      // 传递定义好的数据
      let seldatamw = this.dataSet1
      let distributionData = this.distributionData

      this.$parent.$on('repaint', function (data) {
        let selectedData = []
        // 统计每个工况id出现的次数
        let seldatanum = []
        for (let i = 0; i < data.length; i++) {
          seldatanum.push(seldatamw[data[i]])
        }
        for (var i = 0; i < seldatanum.length; i++) {
          for (var j = 0; j < seldatanum[i]; j++) {
            selectedData.push(distributionData[data[i]])
          }
        }
        // selectedData = selectedData && [0, 0, 0, 0, 0, 0]
        if (selectedData.length !== 0) {
          option = {
            dataset: [{
              source: selectedData
            },
            ...gridOption.dataset
            ]
          }
          option && myChart.setOption(option)
        }
      })
      myChart.setOption(option)
      window.onresize = myChart.resize
    }
  },
  created () {
    this.readFileData()
  },
  mounted () {
    // this.drawDistributionGraph()
  }
}
</script>
<style scoped>
.aaa {
  position: relative;
  /* border: 1px solid rgb(8, 6, 102); */
  width: 100%;
  height: 23vh;
  margin-left: 0%;
  margin-top: 1%;
  background: url(../../assets/images/t_border.png) no-repeat;
  background-size: 100%;
}
</style>



