<template>
  <div>
    <div class="para_container" ref="para_main">
    </div>
    <div style="display:flex;">
      <text-box-set v-for="(item,index) in items" :key="index" :uid="item.uid" />
    </div>
    <input type="button" style="float:center;width:10%;margin-top:5px" value="筛选" @click="this.selectLines">
    <el-button type="primary" size="mini" @click="dialogTableVisible = true">修改参数</el-button>
    <br><br>
    <el-dialog title="参数修改" :visible.sync="dialogTableVisible" width="20%">
      <div>
        <table>
          <tr>
            <td>
              <el-tag>参数</el-tag>
            </td>
            <td>
              <el-select v-model="value" placeholder="请选择" size="small">
                <el-option v-for="item in kinds" :key="item" :label="item" :value="item">
                </el-option>
              </el-select>
            </td>
          </tr>
          <tr>
            <td>
              <el-tag>修改</el-tag>
            </td>
            <td>
              <el-input size="small" v-model="name"></el-input>
            </td>
          </tr>
        </table>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogTableVisible = false">取 消</el-button>
        <el-button size="small" type="primary" @click="doUpdate">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>

import textBoxSet from '../textBoxSet/textBoxSet.vue'
export default {
  name: 'paraMain',
  components: {
    textBoxSet
  },
  data () {
    return {
      name: '',
      value: '',
      kinds:
        [
          localStorage.getItem('name1'),
          localStorage.getItem('name2'),
          localStorage.getItem('name3'),
          localStorage.getItem('name4'),
          localStorage.getItem('name5'),
          localStorage.getItem('name6'),
          localStorage.getItem('name7')
        ],
      dialogTableVisible: false,
      filePath: './static/data/working_condition_data.csv',
      items: [
        { uid: 'textArea_0' },
        { uid: 'textArea_1' },
        { uid: 'textArea_2' },
        { uid: 'textArea_3' },
        { uid: 'textArea_4' },
        { uid: 'textArea_5' },
        { uid: 'textArea_6' },
        { uid: 'textArea_7' }
      ],
      segValues: [], // textBoxSet分割数据
      chart: null, // 此组件图表
      lineData: [], // 画线数据
      allData: [], // 原始数据
      dataLength: 1000 // 显示的数据量长度
    }
  },
  watch: {
    data: function (newData, oldData) {

    }
  },
  methods: {
    doUpdate () {
      var i = 0
      for (i = 0; i < 7; i++) {
        if (this.kinds[i] === this.value) {
          this.kinds[i] = this.name
          break
        }
      }
      let schema = [
        { name: 'id', index: 0, text: '工况id' },
        { name: 'param1', index: 0, text: this.kinds[0] },
        { name: 'param2', index: 1, text: this.kinds[1] },
        { name: 'param3', index: 2, text: this.kinds[2] },
        { name: 'param4', index: 3, text: this.kinds[3] },
        { name: 'param5', index: 4, text: this.kinds[4] },
        { name: 'param6', index: 5, text: this.kinds[5] },
        { name: 'param7', index: 6, text: this.kinds[6] }
      ]
      let option = {
        parallelAxis: [
          { dim: 0, name: schema[0].text },
          { dim: 1, name: schema[1].text },
          { dim: 2, name: schema[2].text },
          { dim: 3, name: schema[3].text },
          { dim: 4, name: schema[4].text },
          { dim: 5, name: schema[5].text },
          { dim: 6, name: schema[6].text },
          { dim: 7, name: schema[7].text }
        ]
      }
      option && this.chart.setOption(option)
      i++
      localStorage.setItem('name' + i.toString(), this.name)
      this.$emit('datachange', i, this.name)
      this.dialogTableVisible = false
    },
    readFileData () {
      this.$axios.get(this.filePath).then(res => {
        let ret = this.$d3.csvParse(res.data)
        let data = []
        let len = this.dataLength > ret.length ? ret.length : this.dataLength
        for (let i = 0; i < len; i++) {
          let temp = Object.keys(ret[i]).map(item => {
            return parseFloat(ret[i][item])
          })
          // temp.shift()
          data.push(temp)
        }
        this.allData = data
        this.drawParallelGraph()
      })
    },
    // 首次绘图
    drawParallelGraph () {
      this.lineData = this.allData
      this.chart = this.$echarts.init(this.$refs.para_main)
      let schema = [
        { name: 'id', index: 0, text: '工况id' },
        { name: 'param1', index: 0, text: this.kinds[0] },
        { name: 'param2', index: 1, text: this.kinds[1] },
        { name: 'param3', index: 2, text: this.kinds[2] },
        { name: 'param4', index: 3, text: this.kinds[3] },
        { name: 'param5', index: 4, text: this.kinds[4] },
        { name: 'param6', index: 5, text: this.kinds[5] },
        { name: 'param7', index: 6, text: this.kinds[6] }
      ]
      let lineStyle = {
        width: 1,
        opacity: 0.5,
        color: '#af3b3b'
      }
      let option = {
        title: {
          left: 'center',
          text: '工况参数平行坐标图',
          padding: [20, 0],
          textStyle: {
            color: 'white'
          }
        },
        color: ['#c23531'],
        animation: false,
        brush: {
          brushLink: 'all',
          throttleType: 'debounce',
          throttleDelay: 500
        },
        tooltip: {
          trigger: 'item',
          formatter: function (params) {
            return params.seriesName + ' id:' + params.data[0]
          }
        },
        toolbox: {
          show: false
        },
        parallelAxis: [
          { dim: 0, name: schema[0].text },
          { dim: 1, name: schema[1].text },
          { dim: 2, name: schema[2].text },
          { dim: 3, name: schema[3].text },
          { dim: 4, name: schema[4].text },
          { dim: 5, name: schema[5].text },
          { dim: 6, name: schema[6].text },
          { dim: 7, name: schema[7].text }
        ],
        parallel: {
          left: '5%',
          right: '9%',
          bottom: '5%',
          top: '25%',
          parallelAxisDefault: {
            type: 'value',
            name: '',
            nameLocation: 'end',
            nameGap: 20,
            nameTextStyle: {
              fontSize: 12,
              color: 'white'
            },
            axisLine: {
              lineStyle: {
                color: 'white'
              }
            }
          }
        },
        series: [
          {
            name: '工况',
            type: 'parallel',
            lineStyle: lineStyle,
            data: this.lineData
          }
        ]
      }
      const Event = this.$parent
      const global = this
      this.chart.on('brushSelected', function (params) {
        let selectedIndex = params.batch[0] && params.batch[0].selected[0] && params.batch[0].selected[0].dataIndex
        // console.log('params', params)
        // 临时处理取消选区事件,暂时未找到相应的api
        let realIndex = []
        if (selectedIndex.length === 0) {
          for (let i = 0; i < global.allData.length; i++) {
            realIndex.push(i)
          }
        } else {
          // 转换成原始数据的index
          for (let i = 0; i < selectedIndex.length; i++) {
            realIndex.push(global.lineData[selectedIndex[i]][0])
          }
        }
        // console.log(selectedIndex)
        if (global.allData.length !== realIndex.length) { Event.$emit('showIndex', realIndex) }
        Event.$emit('repaint', realIndex)
      })

      /* 方法保存 坐标轴选取改变事件 */
      // this.chart.on('axisareaselected', function (params) {
      //   if (params.intervals.length === 0) {
      //     let selectedIndex = []
      //     for (let i = 0; i < this.lineData.length; i++) {
      //       selectedIndex.push(i)
      //     }
      //     Event.$emit('repaint', selectedIndex)
      //   }
      // })

      option && this.chart.setOption(option)
      window.onresize = this.chart.resize
    },
    // 重新绘图
    repaintGraph () {
      let option = {
        series: [
          {
            data: this.lineData
          }
        ]
      }
      option && this.chart.setOption(option)
    },
    getTextBoxValues () {
      this.segValues = []
      for (let i = 0; i < this.items.length; i++) {
        let idName = this.items[i].uid
        let temp = []
        let count = 1
        while (1) {
          let dom = document.getElementById(idName + '_' + count)
          if (!dom) { break }
          temp.push([parseFloat(dom.childNodes[0].childNodes[0].value), parseFloat(dom.childNodes[2].childNodes[0].value)])
          count++
        }
        this.segValues.push(temp)
      }
      // console.log(this.segValues)
    },
    selectLines () {
      this.getTextBoxValues()
      let data = this.allData
      let segValues = this.segValues
      for (let i = 0; i < segValues.length; i++) {
        if (segValues[i].length === 0) continue
        data = data.filter(function (elem) {
          for (let j = 0; j < segValues[i].length; j++) {
            let arr = segValues[i][j]
            if ((isNaN(arr[0]) || elem[i] >= arr[0]) && (isNaN(arr[1]) || elem[i] <= arr[1])) {
              return true
            }
          }
          return false
        })
      }
      this.lineData = data
      this.repaintGraph()
      let realIndex = []
      for (let i = 0; i < data.length; i++) { realIndex.push(data[i][0]) }
      this.$parent.$emit('repaint', realIndex)
      if (this.allData.length !== realIndex.length) { this.$parent.$emit('showIndex', realIndex) }
      // console.log(data)
      // console.log(realIndex)
    }
  },
  created () {
    if (localStorage.getItem('name1') == null) {
      localStorage.setItem('name1', '参数1')
      localStorage.setItem('name2', '参数2')
      localStorage.setItem('name3', '参数3')
      localStorage.setItem('name4', '参数4')
      localStorage.setItem('name5', '参数5')
      localStorage.setItem('name6', '参数6')
      localStorage.setItem('name7', '参数7')
    }
    this.readFileData()
  },
  mounted () {
  },
  destroyed () {
    // clearInterval()
    // this.$parent.off('brushSelected')
  }
}
</script>
<style scoped>
.para_container {
  position: relative;
  /* border: 1px solid rgb(8, 6, 102); */
  width: 100%;
  height: 50vh;
  margin-left: 0%;
  margin-top: 0%;
}
</style>



