<template>
  <div class="mychart1style" id="myChart1">
    <!--    {{ msg }}-->
  </div>
</template>

<script>

import axios from 'axios'

export default {
  name: 'linchart',
  data () {
    return {
      msg: 'hello',
      filePath: './static/data/condition_num.csv',
      // 全局变量数据要在这里声明
      dataIndex: [],
      dataSet: [],
      dataLength: 1000 // 显示的数据量长度
    }
  },
  mounted () {
    this.readdata()
  },
  methods: {
    // 定义方法 读取数据
    readdata () {
      // 定义文件路径
      axios.get(this.filePath).then(res => {
        let ret = this.$d3.csvParse(res.data)
        let data = []
        let len = this.dataLength > ret.length ? ret.length : this.dataLength
        for (let i = 0; i < len; i++) {
          let temp = Object.keys(ret[i]).map(item => {
            return parseFloat(ret[i][item])
          })
          // 删除数组第一项元素 即索引
          temp.shift()
          data.push(temp)
        }
        // console.log('data', data)
        for (let i = 0; i < data.length; i++) {
          this.dataSet.push(data[i][0])
          this.dataIndex.push(i)
        }
        // console.log('dataset', this.dataSet)
        // console.log('dataindex', this.dataIndex)
        // 调用画图函数
        this.drawLine()
      })
    },
    // 定义方法 画图函数 注意:因为异步原因,获取数据和画图函数要分开写
    drawLine () {
      var myChart = this.$echarts.init(document.getElementById('myChart1'))
      var option = {
        yAxis: {
          type: 'category',
          data: this.dataIndex,
          position: 'right'
        },
        xAxis: {
          type: 'value',
          splitNumber: 2,
          splitLine: {
            show: false
          },
          inverse: true

        },
        grid: {
          // top: 100,
          left: '-10%',
          // right: '20%',
          width: '80%',
          height: '80%',
          bottom: 30
        },
        tooltip: {
          trigger: 'item',
          position: 'right',
          formatter: function (params) {
            // console.log(params)
            return '工况id:' + params.dataIndex + ' ' + '次数:' + params.data
          }
        },
        series: [
          {
            data: this.dataSet,
            type: 'line'
          }
        ]
      }
      // console.log('option', option)
      myChart.setOption(option)
    }
  }
}
</script>
<style scoped>
.mychart1style {
  position: relative;
  border: 1px solid rgb(8, 6, 102);
  width: 100%;
  height: 100%;
}
</style>



