<template>
  <div class="stacked_line_container" ref="stacked_line">
  </div>
</template>

<script>
export default {
  name: 'stacked_line',
  data () {
    return {
      filePath: './static/data/',
      lineData: [
        [1, 2, 3, 4],
        [3, 2, 5, 6],
        [2, 2, 4, 5]
      ],
      allData: [
        [1, 2, 3, 4, 5, 7, 3],
        [3, 2, 5, 6, 7, 9, 7],
        [2, 2, 4, 5, 6, 8, 4]],
      chart: null,
      maxSelectLength: 100,
      legendData: [],
      resetChartFlag: false
    }
  },
  props: ['workId', 'fileName', 'title', 'paramsLength', 'number', 'name', 'checkflag'],
  watch: {
    workId: function (newData, oldData) {
      this.drawStackedLineCharts()
    },
    name (newval) {
      this.drawStackedLineCharts()
    },
    checkflag: function (newData, oldData) {
      this.dispatchAction()
    }
  },
  methods: {
    readFileData () {
      this.$axios.get(this.filePath + this.fileName).then(res => {
        this.allData = res.data
        this.drawStackedLineCharts()
      })
    },
    dispatchAction () {
      if (!this.legendData) return
      if (this.checkflag === true) {
        for (let i = 0; i < this.legendData.length; i++) {
          this.chart.dispatchAction({
            type: 'legendUnSelect',
            name: this.legendData[i]
          })
        }
      } else {
        for (let i = 0; i < this.legendData.length; i++) {
          this.chart.dispatchAction({
            type: 'legendSelect',
            name: this.legendData[i]
          })
        }
      }
    },
    drawStackedLineCharts () {
      // 处理数据
      let min = []
      let max = []
      let optimalT = [] // 存储最优值数组的转置

      // 筛选正确的workId
      let tWorkId = []
      for (let i = 0; i < this.workId.length; i++) {
        if (this.allData[this.workId[i]]['value'].length === this.paramsLength) {
          tWorkId.push(this.workId[i])
        }
      }

      let schema = []
      // console.log("this.paramsLength",this.paramsLength)
      for (let i = 0; i < this.paramsLength; i++) {
        let tValue = []
        let tMax = 0
        let tMin = 0
        let tOptimal = []
        for (let j = 0; j < tWorkId.length; j++) {
          let tempVal = this.allData[tWorkId[j]]['value'][i]
          tValue.push(tempVal)
          if (j === 0) {
            tMax = Math.max(...tempVal)
            tMin = Math.min(...tempVal)
          } else {
            if (tMax < Math.max(...tempVal)) { tMax = Math.max(...tempVal) }
            if (tMin > Math.min(...tempVal)) { tMin = Math.min(...tempVal) }
          }
          tOptimal.push(tempVal[tempVal.length - 1].toFixed(2))
        }

        max.push(tMax.toFixed(2))
        min.push(tMin.toFixed(2))
        optimalT.push(tOptimal)
        schema.push(localStorage.getItem(this.paramsLength.toString() + 'name' + (i + 1).toString()))
      }

      // optimalT 进行转置
      var optimal = []
      for (let i = 0; i < optimalT[0].length; i++) {
        optimal[i] = []
      }
      for (let i = 0; i < optimalT.length; i++) {
        for (let j = 0; j < optimalT[i].length; j++) {
          optimal[j][i] = optimalT[i][j]
        }
      }

      this.lineData = []
      this.lineData.push(min)
      this.lineData.push(max)
      this.lineData.push(...optimal)
      // const schema = ['参数1', '参数2', '参数3', '参数4']
      // 设置绘制图表的配置项
      if (this.chart == null || this.chart === '' || this.chart === undefined) {
        this.chart = this.$echarts.init(this.$refs.stacked_line)
      }
      if (this.resetChartFlag) {
        console.log('reserChart:', this.lineData)
        this.chart.dispose()
        this.chart = this.$echarts.init(this.$refs.stacked_line)
        this.resetChartFlag = false
      }
      this.chart.clear()
      // 计算最大值与最小值之间的差值，用于堆叠图的绘制
      let difference = []
      for (let i = 0; i < this.lineData[0].length; i++) {
        difference.push((this.lineData[1][i] - this.lineData[0][i]).toFixed(2))
      }

      // 设置series
      let curSerise = [
        {
          name: '最小值',
          type: 'line',
          stack: 'Total',
          label: {
            show: true,
            position: 'bottom',
            color: 'white'
          },
          color: 'steelblue',
          data: this.lineData[0]
        },
        {
          name: '最大值',
          type: 'line',
          stack: 'Total',
          color: 'steelblue',
          label: {
            show: true,
            position: 'top',
            color: 'white',
            formatter: (param) => {
              return this.lineData[1][param.dataIndex]
            }
          },
          areaStyle: {
            color: 'steelblue'
          },
          data: difference
        }]
      this.legendData = []
      for (let i = 0; i < tWorkId.length; i++) {
        curSerise.push({
          name: '最优值' + tWorkId[i],
          type: 'line',
          color: 'red',
          label: {
            show: true,
            position: 'left',
            color: 'white'
          },
          lineStyle: {
            width: 0.8
          },
          data: this.lineData[i + 2]
        })
        this.legendData.push('最优值' + tWorkId[i])
      }

      let option = {
        animation: false,
        title: {
          left: 'center',
          text: this.title,
          padding: [20, 0],
          textStyle: {
            color: 'white'
          }
        },
        legend: {
          top: '14%',
          left: 'center',
          data: this.legendData,
          itemWidth: 11,
          itemHeight: 9,
          textStyle: {
            fontSize: '9px',
            color: 'white'
          }
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985'
            }
          },
          formatter: (params) => {
            let tipStr = ''
            for (let i = 0; i < params.length; i++) {
              tipStr += params[i].seriesName + ':' + params[i].value + '<br>'
            }
            return tipStr
          }

        },
        xAxis: [
          {
            type: 'category',
            boundaryGap: false,
            data: schema,
            axisLabel: {
              fontSize: 10,
              margin: 20,
              color: 'white'
            },
            axisLine: {
              lineStyle: {
                color: 'white'
              }
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            axisTick: {
              show: false
            },
            axisLine: {
              show: false
            },
            splitLine: {
              show: false
            },
            scale: true,
            splitNumber: 1,
            axisLabel: {
              show: false
            }
          }
        ],
        series: curSerise
      }
      this.chart.setOption(option)
    }
  },
  created () {

  },
  mounted () {
    this.readFileData()
  }
}
</script>
<style scoped>
.stacked_line_container {
  position: relative;
  /* border: 1px solid rgb(8, 6, 102); */
  background: url(../../assets/images/t_border.png) no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: 40vh;
  margin-left: 0%;
  margin-top: 1%;
}
</style>



