<template>
  <div>
    <div class="line_chart_container" ref="line_charts">
    </div>
    <el-button type="primary" size="mini" @click="dialogTableVisible = true">修改参数</el-button>
    <br><br>
    <el-dialog title="参数修改" :visible.sync="dialogTableVisible" width="20%">
      <div>
        <table>
          <tr>
            <td>
              <el-tag>参数</el-tag>
            </td>
            <td>
              <el-select v-model="value" placeholder="请选择" size="small">
                <el-option v-for="item in kinds" :key="item" :label="item" :value="item">
                </el-option>
              </el-select>
            </td>
          </tr>
          <tr>
            <td>
              <el-tag>修改</el-tag>
            </td>
            <td>
              <el-input size="small" v-model="name"></el-input>
            </td>
          </tr>
        </table>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogTableVisible = false">取 消</el-button>
        <el-button size="small" type="primary" @click="doUpdate">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'lineChartList',
  data () {
    return {
      value: '',
      name: '',
      kinds: [],
      dialogTableVisible: false,
      filePath: './static/data/',
      lineDataX: [
        [10, 12, 21, 54, 260, 830, 710],
        [],
        [10, 12, 21, 54, 260, 830, 710],
        [10, 12, 21, 54, 260, 830, 710]
      ],
      lineDataY: [
        [10, 830, 12, 21, 54, 260, 710],
        [],
        [260, 830, 10, 12, 21, 54, 710],
        [12, 21, 54, 260, 830, 10, 710]
      ],
      allData: [],
      chart: null
    }
  },
  props: ['workId', 'fileName', 'title'],
  watch: {
    workId: function (newData, oldData) {
      this.drawLineCharts()
    }
  },
  methods: {
    doUpdate () {
      // console.log(this.kinds)
      var num = this.lineDataX.length
      // console.log("这里呢？",num)
      var i = 0
      for (i = 0; i < num; i++) {
        if (this.kinds[i] === this.value) {
          this.kinds[i] = this.name
          break
        }
      }
      i++
      // localStorage.setItem("2name"+i.toString(),this.name)
      localStorage.setItem(num.toString() + 'name' + i.toString(), this.name)
      this.dialogTableVisible = false
      // this.chart.dispose()
      this.$emit('datachange', i, this.name)
      this.drawLineCharts()
    },
    readFileData () {
      this.$axios.get(this.filePath + this.fileName).then(res => {
        this.allData = res.data
        this.drawLineCharts()
      })
    },
    generateGrids () {
      const GAP = 8
      const BASE_LEFT = 10
      const WIDTH = 100
      const NUM = this.lineDataX.length
      // console.log("长度",NUM)
      const BASE_TOP = 30
      const GRID_WIDTH = (WIDTH - BASE_LEFT) / NUM - GAP
      const GRID_HEIGHT = 40
      let index = 0
      const grid = []
      const xAxis = []
      const yAxis = []
      const series = []
      if (localStorage.getItem(NUM.toString() + 'name1') == null) {
        for (var i = 0; i < NUM; i++) {
          localStorage.setItem(NUM.toString() + 'name' + (i + 1).toString(), '参数' + (i + 1).toString())
        }
      }
      const paramsName = [
        localStorage.getItem(NUM.toString() + 'name1'),
        localStorage.getItem(NUM.toString() + 'name2'),
        localStorage.getItem(NUM.toString() + 'name3'),
        localStorage.getItem(NUM.toString() + 'name4'),
        localStorage.getItem(NUM.toString() + 'name5')
      ]
      for (let i = 0; i < NUM; i++) {
        if (this.kinds.length < NUM) {
          this.kinds.push(localStorage.getItem(NUM.toString() + 'name' + (i + 1).toString()))
        }
        grid.push({
          left: BASE_LEFT + i * (GRID_WIDTH + GAP) + '%',
          top: BASE_TOP + '%',
          width: GRID_WIDTH + '%',
          height: GRID_HEIGHT + '%'
        })
        xAxis.push({
          name: paramsName[i],
          nameLocation: 'center',
          nameTextStyle: {
            padding: [5, 0, 0, 0],
            fontSize: 10,
            color: 'white'
          },
          // splitNumber: 2,
          position: 'bottom',
          type: 'category',
          gridIndex: i,
          boundaryGap: false,
          data: this.lineDataX[i],
          axisLabel: {
            interval: 0,
            fontSize: 8,
            color: 'white'
          }
        })
        yAxis.push({
          type: 'value',
          splitNumber: 3,
          gridIndex: i,
          axisLabel: {
            fontSize: 8,
            color: 'white'
          }
        })
        series.push({
          name: '参数' + (i + 1),
          type: 'line',
          smooth: true,
          data: this.lineDataY[i],
          xAxisIndex: i,
          yAxisIndex: i
        })
        // let tData = []
        // for (let j = 0; j < this.lineDataY[i].length; j++) {
        //   tData[j] = parseFloat(this.lineDataY[i][j]) + 1
        // }
        // console.log(this.lineDataY[i])
        // console.log(tData)
        // series.push({
        //   name: '参数' + (i + 1),
        //   type: 'line',
        //   smooth: true,
        //   data: tData,
        //   xAxisIndex: i,
        //   yAxisIndex: i
        // })
      }
      // console.log('series: ', series)
      return {
        grid,
        xAxis,
        yAxis,
        series
      }
    },
    drawLineCharts () {
      // 处理数据
      let value = this.allData[this.workId]['value']
      let probability = this.allData[this.workId]['probability']
      this.lineDataX = []
      this.lineDataY = []
      for (let i = 0; i < value.length; i++) {
        let tempX = []
        let tempY = []
        // 按X轴二维散点进行排序
        let map = new Map()
        for (let j = 0; j < value[i].length - 1; j++) {
          map.set(value[i][j].toFixed(2), probability[i][j].toFixed(2))
        }
        const mapArray = Array.from(map)
        mapArray.sort((a, b) => { return a[0] - b[0] })
        for (let j = 0; j < value[i].length - 1; j++) {
          tempX.push(mapArray[j][0])
          tempY.push(mapArray[j][1])
        }

        // console.log('map', mapArray)
        // console.log('break :', tempX)
        // console.log('break :', tempY)
        this.lineDataX.push(tempX)
        this.lineDataY.push(tempY)
      }
      // console.log(this.lineDataX)
      // console.log(this.lineDataY)

      // 设置绘图配置项
      if (this.chart != null && this.chart !== '' && this.chart !== undefined) {
        this.chart.dispose()
      }
      this.chart = this.$echarts.init(this.$refs.line_charts)
      let gridOption = this.generateGrids()
      var option
      option = {
        title: {
          left: 'center',
          text: this.title,
          padding: [12, 0],
          textStyle: {
            color: 'white'
          }
        },
        tooltip: {
          trigger: 'axis'
        },
        xAxis: gridOption.xAxis,
        yAxis: gridOption.yAxis,
        grid: gridOption.grid,
        series: [
          ...gridOption.series
        ]
      }

      this.chart.setOption(option)
    }
  },
  created () {
    this.readFileData()
  },
  mounted () {
    // this.drawLineCharts()
  }
}
</script>
<style scoped>
.line_chart_container {
  position: relative;
  /* border: 1px solid rgb(8, 6, 102); */
  background: url(../../assets/images/t_border.png) no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: 20vh;
  margin-left: 0%;
  margin-top: 0%;
}
</style>



