<template>
  <div class="sub_para_container" ref="sub_para">
  </div>
</template>

<script>
export default {
  name: 'paraSub',
  data () {
    return {
      filePath: './static/data/test.csv',
      lineData: [
        [1, 55, 9, 56, 0.46, 18, 6, '良'],
        [2, 25, 11, 21, 0.65, 34, 9, '优'],
        [3, 56, 7, 63, 0.3, 14, 5, '良'],
        [4, 33, 7, 29, 0.33, 16, 6, '优'],
        [5, 42, 24, 44, 0.76, 40, 16, '优'],
        [6, 82, 58, 90, 1.77, 68, 33, '良'],
        [7, 74, 49, 77, 1.46, 48, 27, '良'],
        [8, 78, 55, 80, 1.29, 59, 29, '良'],
        [9, 267, 216, 280, 4.8, 108, 64, '重度污染'],
        [10, 185, 127, 216, 2.52, 61, 27, '中度污染'],
        [11, 39, 19, 38, 0.57, 31, 15, '优'],
        [12, 41, 11, 40, 0.43, 21, 7, '优'],
        [13, 64, 38, 74, 1.04, 46, 22, '良'],
        [14, 108, 79, 120, 1.7, 75, 41, '轻度污染'],
        [15, 108, 63, 116, 1.48, 44, 26, '轻度污染'],
        [16, 33, 6, 29, 0.34, 13, 5, '优'],
        [17, 94, 66, 110, 1.54, 62, 31, '良'],
        [18, 186, 142, 192, 3.88, 93, 79, '中度污染'],
        [19, 57, 31, 54, 0.96, 32, 14, '良'],
        [20, 22, 8, 17, 0.48, 23, 10, '优'],
        [21, 39, 15, 36, 0.61, 29, 13, '优'],
        [22, 94, 69, 114, 2.08, 73, 39, '良'],
        [23, 99, 73, 110, 2.43, 76, 48, '良'],
        [24, 31, 12, 30, 0.5, 32, 16, '优'],
        [25, 42, 27, 43, 1, 53, 22, '优'],
        [26, 154, 117, 157, 3.05, 92, 58, '中度污染'],
        [27, 234, 185, 230, 4.09, 123, 69, '重度污染'],
        [28, 160, 120, 186, 2.77, 91, 50, '中度污染'],
        [29, 134, 96, 165, 2.76, 83, 41, '轻度污染'],
        [30, 52, 24, 60, 1.03, 50, 21, '良'],
        [31, 46, 5, 49, 0.28, 10, 6, '优']
      ]
    }
  },
  watch: {
    data: function (newData, oldData) {

    }
  },
  methods: {
    readFileData () {
      this.$axios.get(this.filePath).then(res => {
        let ret = this.$d3.csvParse(res.data)
        let data = []
        for (let i = 0; i < ret.length; i++) {
          let temp = Object.keys(ret[i]).map(item => {
            return parseFloat(ret[i][item])
          })
          // temp.shift()
          data.push(temp)
        }
        this.lineData = data
        this.drawParallelGraph()
      })
    },
    isInArray (arr, value) {
      for (var i = 0; i < arr.length; i++) {
        if (value === arr[i]) {
          return true
        }
      }
      return false
    },
    // 通过js的内置对象JSON来进行数组对象的深拷贝
    deepClone (obj) {
      let _obj = JSON.stringify(obj)
      let objClone = JSON.parse(_obj)
      return objClone
    },
    drawParallelGraph () {
      let myChart = this.$echarts.init(this.$refs.sub_para)
      let option
      let schema = [
        { name: 'param1', index: 0, text: '参数1' },
        { name: 'param2', index: 1, text: '参数2' },
        { name: 'param3', index: 2, text: '参数3' },
        { name: 'param4', index: 3, text: '参数4' },
        { name: 'param5', index: 4, text: ' 参数5' },
        { name: 'param6', index: 5, text: '参数6' },
        { name: 'param7', index: 6, text: '参数7' }
      ]
      let lineStyle = {
        width: 1,
        opacity: 0.5
      }
      option = {
        color: ['#c23531'],
        animation: false,
        brush: {
          brushLink: 'all',
          throttleType: 'debounce',
          throttleDelay: 500
        },
        tooltip: {
          trigger: 'item',
          formatter: function (params) {
            return ''
          }
        },
        toolbox: {
          show: false
        },
        parallelAxis: [
          { dim: 0, name: schema[0].text },
          { dim: 1, name: schema[1].text },
          { dim: 2, name: schema[2].text },
          { dim: 3, name: schema[3].text },
          { dim: 4, name: schema[4].text },
          { dim: 5, name: schema[5].text }
        ],
        parallel: {
          left: '5%',
          right: '13%',
          bottom: '10%',
          top: '20%',
          parallelAxisDefault: {
            type: 'value',
            name: '',
            nameLocation: 'end',
            nameGap: 20,
            nameTextStyle: {
              fontSize: 12
            }
          }
        },
        series: [
          {
            name: '性能',
            type: 'parallel',
            lineStyle: lineStyle,
            data: []
          }
        ]
      }
      let lineData = this.lineData
      let isInArray = this.isInArray
      let deepClone = this.deepClone
      this.$parent.$on('repaint', function (data) {
        let selectedData = []
        lineData && lineData.forEach(element => {
          if (isInArray(data, element[0])) {
            // 此处不能直接操纵 element，会导致原始数据发生改变，进行深拷贝
            let temp = deepClone(element)
            temp.shift()
            selectedData.push(temp)
          }
        })
        // console.log(selectedData)
        option = {
          series: [
            {
              name: '性能',
              type: 'parallel',
              lineStyle: lineStyle,
              data: selectedData
            }
          ]
        }
        option && myChart.setOption(option)
      })
      option && myChart.setOption(option)
      window.onresize = myChart.resize
    }
  },
  created () {
    this.readFileData()
  },
  mounted () {
    // this.drawParallelGraph()
  }
}
</script>
<style scoped>
.sub_para_container {
  position: relative;
  border: 1px solid rgb(8, 6, 102);
  width: 100%;
  height: 40vh;
  margin-left: 0%;
  margin-top: 0%;
}
</style>



